<template>
  <div>
    <vue-good-table
      :columns="columns"
      :rows="data"
      :pagination-options="{
    enabled: true,
    mode: 'pages',
    position: 'bottom',
    dropdownAllowAll: true,}"
      :search-options="{
    enabled: true,
    placeholder: 'Cari Jukir ',}"
      :selectOptions="{
    enabled: true,
    selectOnCheckboxOnly: true,
    selectionText: 'rows selected',
    clearSelectionText: 'clear',}">

      <template slot="table-row" slot-scope="props">
        <span v-if="props.column.field == 'action'">
          <b-button block variant="primary" @click="infoClicked(props.row)">Info</b-button>
          <b-button block variant="success" @click="updateClicked(props.row)">Update</b-button>
        </span>
        <span v-else-if="props.column.field == 'link_foto'">
          <img :src="'http://' + props.formattedRow[props.column.field]" class="thumbailTablePhoto"/>
        </span>
        <span v-else>
        {{props.formattedRow[props.column.field]}}
        </span>
      </template>
    </vue-good-table>

    <p class="error-msg">{{ errorMsg }}</p>

  </div>
</template>

<script>
  import {EventBus} from '../../_auth/utils/eventbus'

  export default {
    name: 'table-jukir',

    data() {
      return {
        columns: [
          {
            label: 'ID Jukir',
            field: 'id',
            thClass: 'text-center',
          },
          {
            label: 'Foto',
            field: 'link_foto',
            html: 'true',
            tdClass: 'text-center',
            thClass: 'text-center',
            width: '70px'
          },
          // {
          //   label: 'ID/Nama',
          //   field: 'id_and_name',
          //   thClass: 'text-center',
          // },
          {
            label: 'Nama',
            field: 'nm',
            thClass: 'text-center',
          },
          {
            label: 'Jalan',
            field: 'jalan',
            thClass: 'text-center',
          },
          {
            label: 'Action',
            field: 'action',
            thClass: 'text-center',
          },
        ],
        data: [],
        errorMsg: ''
      }
    },

    beforeMount: function () {
      this.getData();
    },

    mounted() {
      EventBus.$on('success', (msg) => {
        this.data = msg;
        this.errorMsg = "";
      });
      EventBus.$on('fail', (msg) => {
        this.data = [];
        this.errorMsg = msg;
      })
    },

    methods: {
      getData() {
        this.$store.dispatch('find_info_jukir_by_range', {})
          .then(() => this.$router.push('/table-jukir'))
      },

      updateClicked(param)
      {
        this.$router.push({
          path:'/detail/detail-jukir',
          query: {id: param.id}
        });
      },
      infoClicked(param)
      {
        this.$router.push({
          path:'/info/info-jukir',
          query: {id: param.id}
        });
      }

    },

    beforeDestroy() {
      EventBus.$off('success');
      EventBus.$off('fail');
    },
  };
</script>
